.cr-sidebar {
  // @include media-breakpoint-down(md) {
  //   display: none;
  // }
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  width: $cr-sidebar-minimized-width;
  font-weight: 200;
  color: #fff;
  background-position: center center;
  background-size: cover;
  transition: width 0.1s ease-in;

  + .cr-content {
    transition: margin 0.1s ease-in;
    margin-left: $cr-sidebar-minimized-width;
  }

  &::after,
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    display: block;
    width: 100%;
    height: 100%;
    content: '';
  }
  .cm-logo {
    margin: 3px auto;
  }
  &::before {
    // background: #000;
    // opacity: 0.33;
  }

  &::after {
    z-index: 3;
    // sublime light
    // background: #fc5c7d; /* fallback for old browsers */
    // background: linear-gradient(to bottom, #6a82fb, #fc5c7d);
    background: #FFF;
    opacity: 1;
  }

  &[data-image]:after {
    opacity: 0.77;
  }

  &__content {
    position: relative;
    z-index: 4;
    height: calc(100vh - 20px);
    padding-bottom: 30px;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;

    &::-webkit-scrollbar {
      width: 0;
    }
    
    .navbar {
      margin-bottom: $spacer * .5;
      border-bottom: $border-width solid $border-color;
    }
  }

  &__background {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    display: block;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover;
    display: none;
  }

  &__nav-item-collapse {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
  }

  &__nav-item-icon {
    // @include media-breakpoint-down(md) {
    //   padding: 0;
    //   margin: 0 auto;
    // }
    width: 1.5rem;
    height: 1.5rem;
    padding-right: 0.5rem;
  }
  .nav {
    margin-top: 20px;

    .nav-item {
      margin: 0;
      color: theme-color(dark);
      border-radius: 4px;
      opacity: 0.86;
    }

    .nav-link {
      // @include media-breakpoint-down(md) {
      //   padding: .5rem;
      // }
      color: theme-color(dark);
      
      >span {
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.5s, opacity 0.5s ease-in;
      }

      &:hover {
        background: rgba(theme-color(dark), 0.15);
      }

      &.active {
        background: rgba(theme-color(dark), 0.10);

        &:after {
          content: '\27E8';
          display: block;
          position: absolute;
          right: 0;
          font-weight: bold;
          width: 30px;
          text-align: center;
        }
        .cr-sidebar__nav-item-icon {
          color: theme-color("secondary");
        }
      }
    }
  }
  .cm-logo-46 {
    display: block;
  }
  .cm-logo-200 {
    display: none;
  }
  &--open {
    left: 0;
    width: $cr-sidebar-width;
    transition: left 0.2s ease-in;

    + .cr-content {
      margin: 0 0 0 $cr-sidebar-width;
      transition: margin 0.2s ease-in;
      .cr-header {
        margin-left: $cr-sidebar-width;
      }
    }
    .nav {
      .nav-link {
        .cr-sidebar__nav-item-icon {
          
        }
        >span {
          visibility: visible;
          opacity: 1;
          transition: visibility 0.5s, opacity 0.5s ease-in;
        }
      }
    }
    .cm-logo-46 {
      display: none;
    }
    .cm-logo-200 {
      display: block;
    }
  }
}
